import { useEffect } from 'react'
import Table from 'react-bootstrap/Table'
import './Users.module.css'
import { Link } from 'react-router-dom'
import useForm from '../hooks/useForm'

const Users = () => {
  const { isLoading, isError, submitForm, data } = useForm()

  useEffect(() => {
    submitForm('/api/users')
  }, [submitForm])

  return (
    <section className='min-height-100vh'>
      <div className='section-wrapper left-align'>
        <h2 className='margin-bottom-20'>Customers</h2>
        <Table>
          <thead>
            <tr>
              <th>UID</th>
              <th>Client Name</th>
              <th>Email</th>
              <th>UEN</th>
              <th>Company Name</th>
            </tr>
          </thead>
          <tbody>
            {data &&
              data.users &&
              data.users.map((customer) => {
                return (
                  <tr key={customer._id}>
                    <td>
                      <Link
                        to={`/user/${customer._id}`}
                        state={{
                          customer,
                        }}
                      >
                        {customer._id}
                      </Link>
                    </td>
                    <td>{customer.name}</td>
                    <td>{customer.email}</td>
                    <td>{customer.uen}</td>
                    <td>{customer.company_name}</td>
                  </tr>
                )
              })}
            {isLoading && (
              <tr>
                <td className='empty-result' colSpan='5'>
                  Fetching users...
                </td>
              </tr>
            )}
            {isError && (
              <tr>
                <td className='empty-result' colSpan='5'>
                  Something went wrong! Refresh the page to try again...
                </td>
              </tr>
            )}
            {!isLoading && !isError && (!data || !data.users || !data.users.length > 0) && (
              <tr>
                <td className='empty-result' colSpan='5'>
                  No users found
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </div>
    </section>
  )
}

export default Users
