import { Navigate, useLocation, Link, useNavigate, useParams } from 'react-router-dom'
import Input from '../components/UI/Input'
import Select from '../components/UI/Select'
import Radio from '../components/UI/Radio'
import formStyles from '../components/authform.module.css'
import './EditShareholder.module.css'
import styles from './User.module.css'
import { useState } from 'react'
import nationalityData from '../files/national.txt'
import countryData from '../files/countries.txt'
import phonecodeData from '../files/phonecode.txt'
import { useEffect } from 'react'
import useForm from '../hooks/useForm'

const EditShareholder = () => {
  const { state } = useLocation()
  const { id } = useParams()
  const [member, setMember] = useState(state ? state.member : null)
  const [nationalityArr, setNationalityArr] = useState(null)
  const [countryArr, setCountryArr] = useState(null)
  const [phonecodeArr, setPhonecodeArr] = useState(null)
  const { isLoading, isError, errorMessage, submitForm } = useForm()
  const navigate = useNavigate()

  useEffect(() => {
    const populateSelectField = (file, stateFunc) => {
      const returnVal = []
      fetch(file)
        .then((r) => r.text())
        .then((text) => {
          const allLines = text.split(/\r\n/)

          allLines.forEach((line) => {
            const valObj = { text: line, value: line }
            returnVal.push(valObj)
          })
          stateFunc(returnVal)
        })
    }
    populateSelectField(nationalityData, setNationalityArr)
    populateSelectField(countryData, setCountryArr)
    populateSelectField(phonecodeData, setPhonecodeArr)
  }, [])

  if (!state) {
    return <Navigate to='/users' />
  }

  const formattedDate = new Date(member.date_of_birth)
    .toISOString()
    .split('T')[0]

  const isDirectorValue =
    typeof member.is_director === 'boolean'
      ? member.is_director
        ? 'yes'
        : 'no'
      : member.is_director
  const isShareholderValue =
    typeof member.is_shareholder === 'boolean'
      ? member.is_shareholder
        ? 'yes'
        : 'no'
      : member.is_shareholder

  const handleInput = (event) => {
    setMember((prev) => {
      return { ...prev, [event.target.name]: event.target.value }
    })
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    console.log('reached the submit handler')
    submitForm(
      `/api/user/${id}?_method=PUT`,
      (data) => {
        console.log(data)
        navigate(`/user/${id}`)
      },
      {
        method: 'POST',
        body: JSON.stringify(member),
      }
    )
  }

  return (
    <section>
      <div className='section-wrapper'>
        <Link className={styles['back-link']} to={`/user/${id}`}>
          Back to user
        </Link>
        <h2>Edit Details</h2>
        <p>
          Note: Always ensure share percentage of all shareholders in an entity
          equals 100%
        </p>
        <form className={formStyles.form} onSubmit={handleSubmit}>
          {isError && <p className={formStyles.error}>{errorMessage}</p>}
          <Input
            label='Full name as in ID'
            inputDetails={{
              name: 'full_name',
              type: 'text',
              required: true,
              value: member.full_name,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Select
            label='ID type'
            inputDetails={{
              name: 'id_type',
              type: 'text',
              required: true,
              value: member.id_type,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={[
              {
                text: 'NRIC (Permanent Resident)',
                value: 'NRIC (Permanent Resident)',
              },
              {
                text: 'NRIC (Singapore Citizen)',
                value: 'NRIC (Singapore Citizen)',
              },
            ]}
          />
          <Input
            label='ID number'
            inputDetails={{
              name: 'id_number',
              type: 'text',
              required: true,
              value: member.id_number,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Radio
            group_name='is_director'
            label='Director?'
            inputValue={isDirectorValue}
            onChange={handleInput}
          />
          <Radio
            group_name='is_shareholder'
            label='Shareholder?'
            inputValue={isShareholderValue}
            onChange={handleInput}
          />
          {isShareholderValue === 'yes' && <Input
            label='Share percentage'
            inputDetails={{
              name: 'share_percentage',
              type: 'number',
              required: true,
              value: member.share_percentage,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />}
          <Input
            label='Email'
            inputDetails={{
              name: 'email',
              type: 'email',
              required: true,
              value: member.email,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Select
            label='Nationality'
            inputDetails={{
              name: 'nationality',
              type: 'text',
              required: true,
              value: member.nationality,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={nationalityArr}
          />
          <Select
            label='Country of birth'
            inputDetails={{
              name: 'country_of_birth',
              type: 'text',
              required: true,
              value: member.country_of_birth,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={countryArr}
          />
          <Input
            label='Date of birth'
            inputDetails={{
              name: 'date_of_birth',
              type: 'date',
              required: true,
              value: formattedDate,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Select
            label='Mobile country code'
            inputDetails={{
              name: 'country_code',
              type: 'text',
              required: true,
              value: member.country_code,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={phonecodeArr}
          />
          <Input
            label='Phone'
            inputDetails={{
              name: 'phone',
              type: 'text',
              required: true,
              value: member.phone,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='House number'
            inputDetails={{
              name: 'local_house_no',
              type: 'text',
              required: true,
              value: member.local_house_no,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Street name'
            inputDetails={{
              name: 'local_street_name',
              type: 'text',
              required: true,
              value: member.local_street_name,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Level'
            inputDetails={{
              name: 'local_level',
              type: 'text',
              required: true,
              value: member.local_level,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Unit number'
            inputDetails={{
              name: 'local_unit_no',
              type: 'text',
              required: true,
              value: member.local_unit_no,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Building/Estate'
            inputDetails={{
              name: 'local_building',
              type: 'text',
              value: member.local_building,
              required: true,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Postal code'
            inputDetails={{
              name: 'local_postal_code',
              type: 'text',
              value: member.local_postal_code,
              required: true,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <button type='submit' className='button' disabled={isLoading}>
            {isLoading ? 'Submitting' : 'Submit'}
          </button>
        </form>
      </div>
    </section>
  )
}

export default EditShareholder
