import styles from './input.module.css'

const Select = (props) => {
  return (
    <>
      <label className={styles.label}>{props.label}</label>
      <select className={styles.input} {...props.inputDetails}>
        {props.options &&
          props.options.map((optionObj, i) => {
            return (
              <option key={i} value={optionObj.value}>
                {optionObj.text}
              </option>
            )
          })}
      </select>
    </>
  )
}

export default Select
