import styles from './input.module.css'

const Radio = (props) => {
  return (
    <div>
      <div>{props.label}</div>
      <label className={styles.radio}>
        <input type='radio' name={props.group_name} required value='yes' checked={props.inputValue === 'yes'} onChange={props.onChange} />
        <span>Yes</span>
      </label>
      <label className={styles.radio}>
        <input type='radio' name={props.group_name} value='no' checked={props.inputValue === 'no'} onChange={props.onChange} />
        <span>No</span>
      </label>
    </div>
  )
}

export default Radio
