import Input from '../components/UI/Input'
import styles from './authform.module.css'
import { useRef } from 'react'
import useForm from '../hooks/useForm'

const AuthForm = (props) => {
  const { isLoading, errorMessage, isError, submitForm } = useForm()

  const type = props.type === 'login' ? true : false
  const subtitle = type
    ? 'Welcome back! Please enter your details.'
    : 'Welcome! Register with Inc'

  const emailRef = useRef(null)
  const passwordRef = useRef(null)

  const handleForm = (e) => {
    e.preventDefault()
    submitForm('/api/login', props.action, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email: emailRef.current.value,
        password: passwordRef.current.value,
      }),
    })
  }

  return (
    <>
      <section className='min-height-100vh'>
        <div className='section-wrapper'>
          <h1 className='margin-bottom-10'>Hey, hello</h1>
          <p className='font-size-18 text-808080'>{subtitle}</p>
          <form className={styles.form} onSubmit={handleForm}>
            {isError && <div className={styles.error}>{errorMessage}</div>}
            <Input
              label='Email'
              className='margin-bottom-20'
              inputDetails={{
                placeholder: 'Enter your email',
                type: 'email',
                autoComplete: 'username',
                name: 'email',
              }}
              ref={emailRef}
            />
            <div className='relative-wrapper'>
              <Input
                label='Password'
                inputDetails={{
                  placeholder: 'Enter your password',
                  name: 'password',
                  type: 'password',
                }}
                ref={passwordRef}
              />
            </div>
            <button disabled={isLoading} className='button margin-top-20'>
              Sign in
            </button>
          </form>
        </div>
      </section>
    </>
  )
}

export default AuthForm
