import useForm from '../hooks/useForm'
import AccordionWrapper from '../components/AccordionWrapper'
import AccordionItem from '../components/AccordionItem'
import SentroWebTable from './SentroWebTable'
import formStyles from '../components/authform.module.css'
import { useState } from 'react'

const SentroWeb = (props) => {
  const { submitForm, data, isLoading, isError, errorMessage } = useForm()
  const { submitForm: updateUserStatus } = useForm()
  const [update, setUpdate] = useState({
    checked: props.checked === 'complete' ? true : false,
    disabled: true,
  })
  const sendArr = []

  props.data &&
    props.data.forEach((item) => {
      const sendObj = {
        country: item.country_of_birth,
        name: item.full_name,
        dateOfBirth: item.date_of_birth,
      }
      sendArr.push(sendObj)
    })

  const screen = () => {
    submitForm('/api/sentroweb', null, {
      method: 'POST',
      body: JSON.stringify(sendArr),
    })
  }

  const updateUser = (event) => {
    setUpdate((prev) => {
      return { checked: !prev.checked, disabled: true }
    })
    updateUserStatus(
      `/api/user/${props._id}/approved`,
      (data) => {
        window.location.reload(false)
      },
      {
        method: 'POST',
        body: JSON.stringify({ result: event.target.checked }),
      }
    )
  }

  return (
    <div className='flex-vertical-start'>
      {isError && <p className={formStyles.error}>{errorMessage}</p>}
      <button className='button' onClick={screen} disabled={isLoading}>
        {isLoading ? 'Loading...' : 'Use SentroWeb API'}
      </button>
      {data &&
        data.result.map((item, i) => {
          return (
            <div key={i} className='width-100'>
              <AccordionWrapper>
                <h4>{item.name}</h4>
                <AccordionItem
                  config={{ eventKey: '1' }}
                  header={`${item.pepPersons.length} Results for Politically Exposed Persons (PEP)`}
                  body={<SentroWebTable data={item.pepPersons} />}
                />
              </AccordionWrapper>
              <AccordionWrapper>
                <AccordionItem
                  config={{ eventKey: '1' }}
                  header={`${item.sipPersons.length} Results for Special Interest Persons (SIP)`}
                  body={<SentroWebTable data={item.sipPersons} />}
                />
              </AccordionWrapper>
            </div>
          )
        })}
      {data && (
        <div>
          <a
            className='button'
            target='_blank'
            rel='noreferrer'
            href='https://dj.ingenique.net/SentroWeb/login'
          >
            Go to SentroWeb
          </a>
        </div>
      )}
      <div className='flex-align-center'>
        <input
          type='checkbox'
          onChange={updateUser}
          style={{ marginBottom: 0, pointerEvents: 'auto' }}
          disabled={update.disabled}
          checked={update.checked}
          title='Click the edit button below to edit this checkbox'
        />
        <label>
          Is this stage complete? If so, click here to update status on users
          dashboard.
        </label>
      </div>
      <button
        onClick={() => {
          setUpdate((prev) => {
            return { ...prev, disabled: false }
          })
        }}
      >
        Edit checkbox
      </button>
    </div>
  )
}

export default SentroWeb
