import { useParams, useLocation, Navigate, useNavigate } from 'react-router-dom'
import Input from '../components/UI/Input'
import formStyles from '../components/authform.module.css'
import { useContext } from 'react'
import useForm from '../hooks/useForm'
import { AuthContext } from '../store/authContext'

const EditImage = () => {
  const { user, member, image } = useParams()
  const { state } = useLocation()
  const { isLoading, isError, errorMessage, submitForm } = useForm()
  const { user: current_user } = useContext(AuthContext)
  const navigate = useNavigate()

  if (!state) {
    return <Navigate to={`/user/${user}`} />
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    const formData = new FormData(e.target)
    formData.append('previous', state)
    for (const pair of formData.entries()) {
      console.log(`${pair[0]}, ${pair[1]}`)
    }
    submitForm(
      `/api/image/${member}/${image}?_method=PUT`,
      (data) => {
        console.log(data)
        navigate(`/user/${user}`)
      },
      {
        method: 'POST',
        body: formData,
        headers: {
          authorization: `Bearer ${current_user.token}`,
        },
      }
    )
  }
  
  const title = image.split('_').join(' ')
  return (
    <section>
      <div className='section-wrapper'>
        <h2>Edit {title}</h2>
        <form className={formStyles.form} onSubmit={handleSubmit}>
          {isError && <p className={formStyles.error}>{errorMessage}</p>}
          <Input
            label={title}
            inputDetails={{
              name: image,
              type: 'file',
              required: true,
            }}
          />
          <button type='submit' className='button' disabled={isLoading}>
            {isLoading ? 'Submitting' : 'Submit'}
          </button>
        </form>
      </div>
    </section>
  )
}

export default EditImage
