import useForm from '../hooks/useForm'
import { useState } from 'react'

const Steps = (props) => {
  const { submitForm: updateUserStatus } = useForm()
  const [update, setUpdate] = useState({
    checked: props.checked === 'complete' ? true : false,
    disabled: true,
  })

  const updateUser = (event) => {
    setUpdate((prev) => {
      return { checked: !prev.checked, disabled: true }
    })
    updateUserStatus(
      `/api/user/${props._id}/${props.status_to_update}`,
      (data) => {
        window.location.reload(false)
      },
      {
        method: 'POST',
        body: JSON.stringify({ result: event.target.checked }),
      }
    )
  }

  return (
    <div className='flex-vertical-start'>
      <div>
        <a className='button' target='_blank' rel='noreferrer' href={props.url}>
          {props.title}
        </a>
      </div>
      <div className='flex-align-center'>
        <input
          type='checkbox'
          onChange={updateUser}
          style={{ marginBottom: 0, pointerEvents: 'auto' }}
          disabled={update.disabled}
          checked={update.checked}
          title='Click the edit button below to edit this checkbox'
        />
        <label>
          Is this stage complete? If so, click here to update status on users
          dashboard.
        </label>
      </div>
      <button
        onClick={() => {
          setUpdate((prev) => {
            return { ...prev, disabled: false }
          })
        }}
      >
        Edit checkbox
      </button>
    </div>
  )
}

export default Steps
