import {
  Navigate,
  useLocation,
  Link,
  useNavigate,
  useParams,
} from 'react-router-dom'
import Input from '../components/UI/Input'
import Select from '../components/UI/Select'
import formStyles from '../components/authform.module.css'
import './EditShareholder.module.css'
import styles from './User.module.css'
import { useState } from 'react'
import activityData from '../files/ssic.txt'
import suffixData from '../files/suffix.csv'
import { useEffect } from 'react'
import useForm from '../hooks/useForm'

const EditEntity = () => {
  const { state } = useLocation()
  const { id } = useParams()
  const [entity, setEntity] = useState(state ? state.entity : null)
  const [suffixArr, setSuffixArr] = useState(null)
  const [activityArr, setActivityArr] = useState(null)
  const { isLoading, isError, errorMessage, submitForm } = useForm()
  const navigate = useNavigate()

  useEffect(() => {
    const populateSelectField = (file, stateFunc) => {
      const returnVal = []
      fetch(file)
        .then((r) => r.text())
        .then((text) => {
          const allLines = text.split(/\r\n/)

          allLines.forEach((line) => {
            const valObj = { text: line, value: line }
            returnVal.push(valObj)
          })
          stateFunc(returnVal)
        })
    }
    populateSelectField(activityData, setActivityArr)
    populateSelectField(suffixData, setSuffixArr)
  }, [])

  if (!state) {
    return <Navigate to='/users' />
  }

  const handleInput = (event) => {
    setEntity((prev) => {
      return { ...prev, [event.target.name]: event.target.value }
    })
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    submitForm(
      `/api/entity/${id}?_method=PUT`,
      (data) => {
        console.log(data)
        navigate(`/user/${id}`)
      },
      {
        method: 'POST',
        body: JSON.stringify(entity),
      }
    )
  }

  return (
    <section>
      <div className='section-wrapper'>
        <Link className={styles['back-link']} to={`/user/${id}`}>
          Back to user
        </Link>
        <h2>Edit Entity</h2>
        <form className={formStyles.form} onSubmit={handleSubmit}>
          {isError && <p className={formStyles.error}>{errorMessage}</p>}
          <Input
            label='Entity name'
            inputDetails={{
              name: 'entity_name',
              type: 'text',
              required: true,
              value: entity.entity_name,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Select
            label='Entity type'
            inputDetails={{
              name: 'type',
              type: 'text',
              required: true,
              value: entity.type,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={[
              {
                text: 'Sole Proprietorship/ Partnership',
                value: 'Sole Proprietorship/ Partnership',
              },
              {
                text: 'Private Company Limited by Shares',
                value: 'Private Company Limited by Shares',
              },
              {
                text: 'Exempt Private Company Limited by Shares',
                value: 'Exempt Private Company Limited by Shares',
              },
              {
                text: 'Limited Partnership (LP)',
                value: 'Limited Partnership (LP)',
              },
              {
                text: 'Limited Liability Partnership (LLP)',
                value: 'Limited Liability Partnership (LLP)',
              },
            ]}
          />
          <Select
            label='Entity suffix'
            inputDetails={{
              name: 'suffix',
              type: 'text',
              required: true,
              value: entity.suffix,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={suffixArr}
          />
          <Select
            label='Company activity'
            inputDetails={{
              name: 'activity',
              type: 'text',
              required: true,
              value: entity.activity,
              onChange: (event) => {
                handleInput(event)
              },
            }}
            options={activityArr}
          />
          <Input
            label='House / block number'
            inputDetails={{
              name: 'registered_office_block_number',
              type: 'text',
              required: true,
              value: entity.registered_office_block_number,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Street name'
            inputDetails={{
              name: 'registered_office_street',
              type: 'text',
              required: true,
              value: entity.registered_office_street,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Level'
            inputDetails={{
              name: 'registered_office_level',
              type: 'text',
              required: true,
              value: entity.registered_office_level,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Unit number'
            inputDetails={{
              name: 'registered_office_unit',
              type: 'text',
              required: true,
              value: entity.registered_office_unit,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Building/Estate'
            inputDetails={{
              name: 'registered_office_building',
              type: 'text',
              value: entity.registered_office_building,
              required: true,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <Input
            label='Postal code'
            inputDetails={{
              name: 'registered_office_postal_code',
              type: 'text',
              value: entity.registered_office_postal_code,
              required: true,
              onChange: (event) => {
                handleInput(event)
              },
            }}
          />
          <button type='submit' className='button' disabled={isLoading}>
            {isLoading ? 'Submitting' : 'Submit'}
          </button>
        </form>
      </div>
    </section>
  )
}

export default EditEntity
