import AccordionWrapper from '../components/AccordionWrapper'
import AccordionItem from '../components/AccordionItem'
import { Link, useParams } from 'react-router-dom'
import styles from './User.module.css'
import { useEffect, useState } from 'react'
import useForm from '../hooks/useForm'
import Shareholder from '../components/Shareholder'
import Entity from '../components/Entity'
import SentroWeb from '../components/SentroWeb'
import Steps from '../components/Steps'
import { CSVLink } from 'react-csv'
import { formatDate } from '../helpers'

const User = () => {
  const { id } = useParams()
  const {
    isLoading,
    isError,
    errorMessage,
    submitForm,
  } = useForm()
  const [customer, setCustomer] = useState(null)

  useEffect(() => {
    submitForm(`/api/user/${id}`, (data) => {
      setCustomer(data)
    })
  }, [submitForm, id])

  const csvData = []
  const userDetails = customer && [...customer.board_members, customer.entity]
  userDetails &&
    userDetails.forEach((item) => {
      const role = item.type
        ? 'New Entity'
        : item.is_shareholder && item.is_director
        ? 'Director'
        : item.is_shareholder
        ? 'Shareholder'
        : item.is_director
        ? 'Director'
        : ''
      const itemRef = {
        Role: role,
        Ind_Individual_Name: item.full_name,
        Ind_Individual_Former_Name: '',
        Ind_Alias: '',
        Ind_Country_of_Birth: item.country_of_birth,
        Ind_Nationality: item.nationality,
        Ind_Gender: '',
        Ind_Date_of_Birth: item.type ? '' : formatDate(item.date_of_birth),
        Ind_Risk_Assessm: '',
        Ind_Rating: '',
        Ind_Status: item.type ? '' : 'Active',
        Ind_Identification_type: item.id_type,
        Ind_ID_No: item.id_number,
        Ind_ID_Issued_Country: '',
        Ind_ID_Issued_Date: '',
        Ind_ID_Expiry_Date: '',
        Ind_Date_Of_Appointment: item.type
          ? ''
          : formatDate(customer.entity.entity_incorporation_date * 1000),
        Ind_Additional_Notes: '',
        Is_Shareholder: item.is_shareholder,
        Share_Pct: item.share_percentage,
        Is_Contact: item.type ? '' : false,
        Is_Controller: false,
        Ind_Default_Address: item.type ? '' : 'Local Address',
        'Ind_Local_House_No./Block': item.local_house_no,
        Ind_Local_Street_Name: item.local_street_name,
        'Ind_Local_Building/Estate': item.local_building,
        Ind_Local_Level: item.local_level,
        Ind_Local_Unit_No: item.local_unit_no,
        Ind_Local_Country: item.type ? '' : 'SINGAPORE',
        'Ind_Local_State/Province': '',
        'Ind_Local_City_and/or_Town': '',
        Ind_Local_Postal_Code: item.local_postal_code,
        Ind_Other_Address_Type: '',
        Ind_Other_Address_Line1: '',
        Ind_Other_Address_Line2: '',
        Ind_Preferred_Contact_Mode: '',
        Ind_Email_Address: item.email,
        Ind_Skype_ID: '',
        Ind_Mobile_Number_Country: item.country_code,
        Ind_Mobile_Number: item.phone,
        Ind_Telephone_Number_Country: item.country_code,
        Ind_Telephone_Number: '',
        Ind_Fax_Country: item.country_code,
        Ind_Fax: '',
        Ind_Secretarial: item.type ? '' : false,
        Ind_Accounts: item.type ? '' : false,
        Ind_Tax_Report: item.type ? '' : false,
        Ind_Billing: item.type ? '' : false,
        Ind_Audit: item.type ? '' : false,
        Ind_All_Matters: item.type ? '' : false,
        Ent_CSS_Client: item.type ? false : '',
        Ent_Auditor: item.type ? false : '',
        Ent_Taxation_Client: item.type ? false : '',
        Ent_Corporate_Shareholder: item.type ? false : '',
        Ent_Fund_Management: item.type ? false : '',
        Ent_Accounting_Client: item.type ? false : '',
        Ent_Corporate_Director: item.type ? false : '',
        Ent_Corporate_Owner: item.type ? false : '',
        Ent_External_Corp_Sec: item.type ? false : '',
        Ent_Audit_Client: item.type ? false : '',
        Ent_Prospect: item.type ? false : '',
        Ent_Client: item.type ? false : '',
        'Ent_Non-Client': item.type ? false : '',
        Ent_Entity_Name: item.entity_name,
        Ent_Former_Name_if_any: '',
        Ent_Company_ID: '',
        Ent_Entity_Type: item.type,
        'Ent_Registration_No.': '',
        Ent_ACRA_UEN: '',
        Ent_Country: item.type ? 'SINGAPORE' : '',
        Ent_Region: '',
        Ent_Status: item.type ? 'Active' : '',
        Ent_Risk_Assessment_Rating: '',
        Ent_Incorporation_Date: item.type
          ? formatDate(customer.entity.entity_incorporation_date * 1000)
          : '',
        Ent_Internal_CSS_Status: '',
        'Ent_Articles/Constitution': '',
        'Ent_Article/Regulation_No.': '',
        Ent_Article_Description: '',
        Ent_Common_Seal: '',
        Ent_Company_Stamp: '',
        Ent_Legislation_Entity_Name: '',
        Ent_corporate_controller: item.type ? 'Companies Act' : '',
        Ent_Name_of_the_Corporate_Entity_Register: '',
        Ent_Identification_Number_or_Registration_Number: '',
        Ent_Default_Address: item.type ? 'Registered Office Address' : '',
        'Ent_House_No./Block': item.registered_office_block_number,
        Ent_Street_Name: item.registered_office_street,
        Ent_Building: item.registered_office_building,
        Ent_Level: item.registered_office_level,
        Ent_Unit_No: item.registered_office_unit,
        Ent_Addr_Country: item.type ? 'SINGAPORE' : '',
        'Ent_State/Province': '',
        'Ent_City_and/or_Town': '',
        Ent_Postal_Code: item.registered_office_postal_code,
        Ent_Other_Address_Type: '',
        Ent_Address_Line1: '',
        Ent_Address_Line2: '',
        'Ent_SSIC_Code_Activity_(I)': item.activity,
        'Ent_SSIC_Code_Num_(I)': item.activity_number,
        'Ent_SSIC_Code_Activity_(II)': '',
        'Ent_SSIC_Code_Num_(II)': '',
        Ent_User_Described_Activity1: '',
        Ent_User_Described_Activity2: '',
        Ent_Country_Company_Phone1: item.type ? 'SINGAPORE - (65)' : '',
        Ent_Company_Phone1: '',
        Ent_Country_Company_Phone2: item.type ? 'SINGAPORE - (65)' : '',
        Ent_Company_Phone2: '',
        Ent_Country_Fax: item.type ? 'SINGAPORE - (65)' : '',
        Ent_Fax: '',
        Ent_Website: '',
        Ent_Company_Email_Address: '',
        Ent_Remarks: '',
        Ent_Additional_Remarks: '',
        Ent_Suffix: item.suffix,
        Record: item.type ? 2 : 1,
      }
      csvData.push(itemRef)
    })

  return (
    <section className='min-height-100vh'>
      <div className='section-wrapper left-align'>
        <Link className={styles['back-link']} to={'/users'}>
          Back to all users
        </Link>
        {customer && <p>Registration email: {customer.email}</p>}
        {isLoading && !customer && <p>Fetching user data</p>}
        {isError && <p>{errorMessage}</p>}
        {!isLoading && !isError && !customer && <p>No user data</p>}
        {customer && (
          <>
            <CSVLink
              className={styles.export}
              data={csvData}
              filename={`customer-${customer._id}.csv`}
            >
              Export as csv
            </CSVLink>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='Shareholders/Directors'
                body={
                  <Shareholder
                    _id={customer._id}
                    data={customer.board_members}
                  />
                }
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='Entity Details'
                body={<Entity data={customer.entity} _id={customer._id} />}
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='SentroWeb'
                body={
                  <SentroWeb
                    _id={customer._id}
                    data={customer.board_members}
                    checked={customer.status.approved}
                  />
                }
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='Regtank'
                body={
                  <div>
                    <a
                      className='button'
                      target='_blank'
                      rel='noreferrer'
                      href='https://regtank.com/'
                    >
                      Open Regtank
                    </a>
                  </div>
                }
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='Teamworks'
                body={
                  <Steps
                    url='https://apps.teamworkcss.com/corporatefinedge/'
                    checked={customer.status.screening_complete}
                    title='Open Teamworks'
                    _id={customer._id}
                    status_to_update='screening_complete'
                  />
                }
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='HelloSign'
                body={
                  <Steps
                    url='https://www.hellosign.com/'
                    checked={customer.status.signatures_complete}
                    title='Open HelloSign'
                    _id={customer._id}
                    status_to_update='signatures_complete'
                  />
                }
              />
            </AccordionWrapper>
            <AccordionWrapper>
              <AccordionItem
                config={{ eventKey: '1' }}
                header='ACRA'
                body={
                  <Steps
                    url='https://www.acra.gov.sg/'
                    checked={customer.status.acra}
                    title='Open ACRA'
                    _id={customer._id}
                    status_to_update='acra'
                  />
                }
              />
            </AccordionWrapper>
          </>
        )}
      </div>
    </section>
  )
}

export default User
