import './Shareholder.module.css'
import { Link } from 'react-router-dom'
import { formatDate } from '../helpers'

const Shareholder = (props) => {
  return (
    <>
      {props.data.length &&
        props.data.map((member) => {
          return (
            <div key={member._id}>
              <h3>{member.full_name}</h3>
              <div className='grid'>
                <p>
                  Full name: <span>{member.full_name}</span>
                </p>
                <p>
                  ID type: <span>{member.id_type}</span>
                </p>
                <p>
                  ID number: <span>{member.id_number}</span>
                </p>
                <p>
                  Is director: <span>{member.is_director ? 'yes' : 'no'}</span>
                </p>
                <p>
                  Is shareholder:{' '}
                  <span>{member.is_shareholder ? 'yes' : 'no'}</span>
                </p>
                {member.is_shareholder && (
                  <p>
                    Share percentage: <span>{member.share_percentage}%</span>
                  </p>
                )}
                <p>
                  Email: <span>{member.email}</span>
                </p>
                <p>
                  Nationality: <span>{member.nationality}</span>
                </p>
                <p>
                  Country of birth: <span>{member.country_of_birth}</span>
                </p>
                <p>
                  Date of birth: <span>{formatDate(member.date_of_birth)}</span>
                </p>
                <p>
                  Country code: <span>{member.country_code}</span>
                </p>
                <p>
                  Phone number: <span>{member.phone}</span>
                </p>
              </div>
              <p className='margin-top-20 font-weight-700'>Local address</p>
              <div className='grid'>
                <p>
                  House number: <span>{member.local_house_no}</span>
                </p>
                <p>
                  Level: <span>{member.local_level}</span>
                </p>
                <p>
                  Building: <span>{member.local_building}</span>
                </p>
                <p>
                  Unit number: <span>{member.local_unit_no}</span>
                </p>
                <p>
                  Street: <span>{member.local_street_name}</span>
                </p>
                <p>
                  Postal code: <span>{member.local_postal_code}</span>
                </p>
                <div>
                  <p>NRIC front</p>
                  <img src={`/files/${member.nric_front}`} alt='nric_front' />
                  <Link
                    to={`/${props._id}/${member._id}/images/nric_front`}
                    state={member.nric_front}
                  >
                    Change image
                  </Link>
                </div>
                <div>
                  <p>NRIC back</p>
                  <img src={`/files/${member.nric_back}`} alt='nric_back' />
                  <Link
                    to={`/${props._id}/${member._id}/images/nric_back`}
                    state={member.nric_back}
                  >
                    Change image
                  </Link>
                </div>
              </div>
              <div>
                <Link
                  to={`/member/${props._id}`}
                  state={{
                    member,
                  }}
                  className='button margin-top-40'
                >
                  Edit
                </Link>
              </div>
            </div>
          )
        })}
      {!props.data.length && (
        <p className='empty-result'>No shareholder/director added yet</p>
      )}
    </>
  )
}

export default Shareholder
