import Table from 'react-bootstrap/Table'

const SentroWebTable = (props) => {
  return (
    <Table>
      <thead>
        <tr>
          <th>ID</th>
          <th>Action</th>
          <th>Date</th>
          <th>Gender</th>
        </tr>
      </thead>
      <tbody>
        {props.data &&
          props.data.map((person) => {
            return (
              <tr key={person.id}>
                <td>{person.id}</td>
                <td>{person.action}</td>
                <td>{person.date}</td>
                <td>{person.gender}</td>
              </tr>
            )
          })}
      </tbody>
    </Table>
  )
}

export default SentroWebTable
